import {BrowserRouter, Routes ,Route} from 'react-router-dom';
import './App.css';
import Blog from './views/Blog';
import ListadoPeliculas from './views/ListadoPeliculas';

function App() {
  return(
	<BrowserRouter>
		<Routes>
			<Route path="/">
				<Route index element={<ListadoPeliculas />} />
				<Route path="blog" element={<Blog />} />			
			</Route>			
		</Routes>  
	</BrowserRouter>		
  );  
}

export default App;
